import {
  Navbar,
  Container,
  Offcanvas,
  Nav,
  Form,
  FormControl,
  Image,
} from 'react-bootstrap';

import styles from './styles.module.scss';

import logo from '../../assets/images/safa-logo.svg';
import calendar from '../../assets/images/calendar.svg';
import { BsSearch } from 'react-icons/bs';
import { useTranslation } from 'react-i18next';
import Icon from '../Shared/Icon/Icon';
import { Link } from 'react-router-dom';


export default function NavOne() {
  const { t, i18n } = useTranslation();

  return (
    <div className={styles.navOne}>
      <Navbar bg="transparent" className="mb-3" expand="lg">
        <Container fluid>
          <Navbar.Brand href="/">
            <Image src={logo} alt="Safa Logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls={`offcanvasNavbar-expand`} />
          <Navbar.Offcanvas
            id={`offcanvasNavbar-expand`}
            aria-labelledby={`offcanvasNavbarLabel-expand`}
            placement="end"
          >
            <Offcanvas.Header closeButton>
              <Offcanvas.Title id={`offcanvasNavbarLabel-expand`}>
                القائمة الرئيسية
              </Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <Nav className="justify-content-end flex-grow-1 pe-3 gap-5">
                {/* <Nav.Link href="#action1" className={`m-3 ${styles.link}`}>
                  EN
                </Nav.Link> */}
                {
                  i18n.language === "en" && <button className={`m-3 btn fs-5`} style={{ background: "transparnt", color: '#fff' }} onClick={() => {
                    i18n.changeLanguage("ar")
                  }}>
                    AR
                  </button>
                }
                {
                  i18n.language === "ar" && <button className={`m-3 btn fs-5`} style={{ background: "transparnt", color: '#fff' }} onClick={() => {
                    i18n.changeLanguage("en")
                  }}>
                    EN
                  </button>
                }
                <Form className="d-flex  align-items-center">
                  <BsSearch className="text-white align-items-center" />
                  <FormControl
                    type="search"
                    placeholder={t('navOne.placeholder')}
                    className="text-white"
                    aria-label="Search"
                  />
                </Form>
                <Nav.Link
                  href="#action1"
                  className={`gap-3 ${styles.calendar}`}
                >
                  <Image src={calendar} alt="calendar Icon" className="m-3" />
                  <span>{t('navOne.agenda')}</span>
                </Nav.Link>
                <Link to="/profile">
                  <Icon iconName="profile" iconSize={50} />
                </Link>
              </Nav>
            </Offcanvas.Body>
          </Navbar.Offcanvas>
        </Container>
      </Navbar>
    </div>
  );
}
