import Icons from "./IconsSvg.svg";

interface IIconProps {
  iconName: string,
  iconSize?: number,
  className?: string,
  style?: object,
  variant?: "primary" | "secondary" | "warning" | "white" | "danger" | "success" | "white" | "black" | "dark" | "blue" | "gray-400",
  height?: number,
  hasStroke?: boolean
}


const Icon = ({
  iconName,
  iconSize = 20,
  className =  "",
  style,
  variant,
  height,
  hasStroke = false
}: IIconProps) => {

  const ICONCOLOR: any = {
    primary: "#5260ab",
    secondary: "#51bfad",
    warning: "#ffd36f",
    danger: "#e30613",
    success: "#36b600",
    white: "#fff",
    black: "#000",
    dark: "#333",
    blue: "#303a6c",
    "gray-400": "#ddd"
  }

  return (
    <svg
      className={`icon icon-${iconName} ${className}`}
      width={iconSize}
      height={height ? height : iconSize}
      xmlns="http://www.w3.org/2000/svg"
      style={style}
      fill={ICONCOLOR[variant ?? "white"]}
      stroke={hasStroke ? variant : ""}
    >
      <use xlinkHref={`${Icons}#${iconName}`} />
    </svg>
  )
}

export default Icon;