import Breadcrumb from 'react-bootstrap/Breadcrumb';
import { useTranslation } from 'react-i18next';

import styles from './styles.module.scss';

interface IBreadCrumbsProps {
  breadCrumbs: string[];
  title?: string,
}

export default function BreadCrumbs({ breadCrumbs, title }: IBreadCrumbsProps) {
  const { t } = useTranslation();
  return (
    <>
      {
        title && <h2 className={`text-white ${styles.title}`}> {title} </h2>
      }
      <Breadcrumb>
        {breadCrumbs.map((breadCrumb, index) => (
          <Breadcrumb.Item className={styles.item} key={index}>
            {t(`${breadCrumb}`)}
          </Breadcrumb.Item>
        ))}
      </Breadcrumb>
    </>
  );
}
